option{
  margin:40px;
  background: rgb(0, 0, 0);
  color:black;
  text-shadow:0 1px 0 rgba(0,0,0,0.4);
}
.header{
  font-size: 1.2rem;
  width: 100vw;

}

  

.MuiCard-root {
    margin: 10px;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    align-content: flex-end;
    justify-content: space-around;
    align-items: baseline;
    text-align: center;
}

.MuiTypography-body2 {
    font-size: 0.875rem;
    font-family: "Roboto";
    font-weight: 400;
    line-height: 1.43;
    letter-spacing: 0.01071em;
    /* font-family: 'Exo 2', sans-serif; */
}

.MuiPaper-root {
    color: white !important;
    transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    background-color: rgba(0, 0, 0, 0) !important;
    
}

.MuiTypography-colorTextSecondary {
    color: white !important;
}

.MuiCardContent-root {
    padding: 16px;
    margin-top: 3% !important;
}

.confirmed {
    border-bottom: 10px solid blue;
    margin: 0 5%;
    background: #000000d9;
  }

.confirmed:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    border-radius: 20px;
    border: 2px solid blue;
}

.recovered {
    border-bottom: 10px solid #62ff00;
    margin: 0 5%;
    background: #000000d9;
 
    
}
.recovered:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    border-radius: 20px;
    border: 2px solid #62ff00;
}

.deaths {
    border-bottom: 10px solid red;
    padding: 1px 1px;
    margin: 0 5%;
    background: #000000d9;


}
.deaths:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    border-radius: 20px;
    border: 2px solid red;
}

.MuiPaper-elevation1 {
    box-shadow: none !important;
}

.green, .blue , .red {
    font-family: 'Righteous', cursive;
    font-size: 1.3rem;
}

.blue {
    color: blue;
}

.red {
    color: red;
}

.green {
    color: #62ff00;
}

.date {
    color: #ffffff5e;
    font-family: 'EB Garamond', serif;
}

.Chart{
    width: 60vw;

    margin: auto;
    margin-top: 4%;
    color: white;
   
  
}
